@import '~src/css/quasar.variables.styl'
@import '~quasar/src/css/variables.styl'



































.log
    flex-grow: 2
    align-self: stretch
    min-width: 50vw
    max-width: 100vw
    max-height: calc(100vh - 15em);
    min-height: 5em;
    overflow: auto
    background-color: hsla(0, 0, 0, 0.05)
    font-size: 1em
    line-height: 129%
    font-family: Overpass-mono
.log .info
    background $info
    min-width 3em
    text-align right
.log pre
    margin: 0
    white-space: pre
    font-size: 1em
    line-height: 129%
    font-family: Overpass-mono
